@use 'base/variables' as v;

.testimonial {
    background-color: v.$verde;
    font-size: 2.4rem;
    padding: 2rem;
    color: v.$blanco;
    border-radius: 2rem;

    blockquote {
        padding-left: 5rem;
        position: relative;

        &::before {
            content: '';
            background-image: url('../img/comilla.svg');
            background-repeat: no-repeat;
            // content: open-quote;
            width: 6rem;
            height: 6rem;
            display: block;
            font-size: 10rem;
            position: absolute;
            left:-2rem;
        }
    }

    p {
        text-align: right;
        color: v.$blanco;
    }
}